<template>
    <div name="mycal" class="mycal">
        <form class="form-inline" style="height:385px;">
            <div class="form-group col-xs-12 col-lg-12 edit-block">
                <label>Team</label>
                <input type="text" class="form-control" v-model="model.teamName" />
            </div>
            <div class="form-group col-xs-3 col-lg-3 edit-block">
                <label>Home Score6</label>
                <input type="number" class="form-control" v-model="model.hs6" />
            </div>
            <div class="form-group col-xs-3 col-lg-3 edit-block">
                <label>Home Lose6</label>
                <input type="number" class="form-control" v-model="model.hl6" />
            </div>
            <div class="form-group col-xs-3 col-lg-3 edit-block">
                <label>Home Score</label>
                <input type="number" class="form-control" v-model="model.hs" />
            </div>
            <div class="form-group col-xs-3 col-lg-3 edit-block">
                <label>Home Lose</label>
                <input type="number" class="form-control" v-model="model.hl" />
            </div>
            <div class="form-group col-xs-3 col-lg-3 edit-block">
                <label>GS6</label>
                <input type="number" class="form-control" v-model="model.gs6" />
            </div>
            <div class="form-group col-xs-3 col-lg-3 edit-block">
                <label>GL6</label>
                <input type="number" class="form-control" v-model="model.gl6" />
            </div>
            <div class="form-group col-xs-3 col-lg-3 edit-block">
                <label>GS</label>
                <input type="number" class="form-control" v-model="model.gs" />
            </div>
            <div class="form-group col-xs-3 col-lg-3 edit-block">
                <label>GL</label>
                <input type="number" class="form-control" v-model="model.gl" />
            </div>
            <div class="form-group col-xs-3 col-lg-3 edit-block">
                <label>HR</label>
                <input type="number" class="form-control" v-model="model.hr" />
            </div>
            <div class="form-group col-xs-3 col-lg-3 edit-block">
                <label>GR</label>
                <input type="number" class="form-control" v-model="model.gr" />
            </div>
            <div class="form-group col-xs-3 col-lg-3 edit-block">
                <label>HT</label>
                <input type="number" class="form-control" v-model="model.ht" />
            </div>
            <div class="form-group col-xs-3 col-lg-3 edit-block">
                <label>GT</label>
                <input type="number" class="form-control" v-model="model.gt" />
            </div>
            <div class="form-group col-xs-3 col-lg-3 edit-block">
                <label>HSA</label>
                <input type="number" class="form-control" v-model="model.hsa" />
            </div>
            <div class="form-group col-xs-3 col-lg-3 edit-block">
                <label>HLA</label>
                <input type="number" class="form-control" v-model="model.hla" />
            </div>
            <div class="form-group col-xs-3 col-lg-3 edit-block">
                <label>GSA</label>
                <input type="number" class="form-control" v-model="model.gsa" />
            </div>
            <div class="form-group col-xs-3 col-lg-3 edit-block">
                <label>GLA</label>
                <input type="number" class="form-control" v-model="model.gla" v-on:keyup.enter="calculate" />
            </div>
        </form>
        <div style="padding:15px 15px 0 15px;">
            <table class="table table-striped table-bordered">
                <thead>
                    <tr>
                        <th width="33%">State3</th>
                        <th width="33%">State6</th>
                        <th>State All</th>
                    </tr>
                </thead>
                <tbody>
                    <tr class="success">
                        <td>{{target.l1+ ' - ' + target.r1}}</td>
                        <td>{{target.l2 + ' - ' + target.r2}}</td>
                        <td>{{target.l3 +' - ' + target.r3}}</td>
                    </tr>
                </tbody>
            </table>
        </div>
        <div class="btn-group btn-group-lg">
            <button class="btn btn-success" v-on:click='calculate'>Cal</button>
            <button class="btn btn-success" v-on:click='reset'>Reset</button>
        </div>
    </div>
</template>

<script>
import Store from '../services/store2';
export default {
    name: 'six',
    data() {
        return {
            model: {
                hs: '',
                hs6: '',
                hsa: '',
                hl: '',
                hl6: '',
                hla: '',
                gs: '',
                gs6: '',
                gsa: '',
                gl: '',
                gl6: '',
                gla: '',
                hr: '',
                gr: ''
            },
            target: {
                l1: '',
                l2: '',
                l3: '',
                r1: '',
                r2: '',
                r3: '',
                team: ''
            },
            modelList: Store.fetch().length > 0 ? Store.fetch() : []
        };
    },
    methods: {
        calculate: function () {
            // 转为数字
            for (let item in this.model) {
                if (item !== 'teamName') {
                    this.model[item] = parseFloat(this.model[item]);
                }
            }
            let rate = this.sortRate(this.model.gr - this.model.hr);
            this.target.l1 = ((this.model.hs + this.model.gl) / 6 * rate.h).toFixed(2);
            this.target.l2 = ((this.model.hs6 + this.model.gl6) / 12 * rate.h).toFixed(2);
            this.target.l3 = (
                (this.model.hsa + this.model.gla) /
                (this.model.ht + this.model.gt)
            ).toFixed(2);
            this.target.r1 = ((this.model.gs + this.model.hl) / 6 * rate.g).toFixed(2);
            this.target.r2 = ((this.model.gs6 + this.model.hl6) / 12 * rate.g).toFixed(2);
            this.target.r3 = (
                (this.model.gsa + this.model.hla) /
                (this.model.ht + this.model.gt)
            ).toFixed(2);
            if (this.model.teamName) {
                this.target.team = this.model.teamName;
                this.modelList.unshift(this.target);
                Store.save(this.modelList);
            } else {
                alert('请输入队名！');
            }
        },
        sortRate: function (diff) {
            let rate = {
                h: 1,
                g: 1
            };
            if (diff > 3 && diff <= 6) {
                rate.h += 0.05;
                rate.g -= 0.15;
            } else if (diff > 6) {
                rate.h += 0.1;
                rate.g -= 0.25;
            } else if (diff < -6) {
                rate.g += 0.1;
                rate.h -= 0.25;
            } else if (diff >= -6 && diff < -3) {
                rate.g += 0.05;
                rate.h -= 0.15;
            }
            return rate;
        },
        reset: function () {
            this.model = {
                hs: '',
                hs6: '',
                hsa: '',
                hl: '',
                hl6: '',
                hla: '',
                gs: '',
                gs6: '',
                gsa: '',
                gl: '',
                gl6: '',
                gla: ''
            };
            this.target = {
                l1: '',
                l2: '',
                l3: '',
                r1: '',
                r2: '',
                r3: '',
                team: ''
            };
        }
    }
};
</script>



